html, body, #root{
  background-color:rgb(33, 33, 33);
  color:white;
  font-family:sans-serif;
  height: 100%;
  margin: 0;
  width: 100%;
}

.App{
  height: 100%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.topbox{
  border: 1px solid red;
  display: block;
  width: 30%;
  margin: 0 auto;
}


.botbox{
  border: 1px solid green;
  width: 50%;
  margin: 0 auto;
  padding-top: 0px;
  margin-top: 25px;
}

#editor{
  min-width: fill;
  max-width: fill;
  height: fill;
  min-height: 250px;
  margin-top: 0px;
}

#preview{
  background-color:rgb(158, 158, 158);
  min-width: fill;
  max-width: fill;
  min-height: 500px;
  margin: 0;
  padding: 5px 15px 15px 15px;
}

#boxlabel {
  text-align: center;
  padding: 10px;
  margin: 0;
  color:rgba(202, 202, 202, 0.959);
}
